import { dataStore } from './data-store'
import { supabaseStore } from './supabase-store'

// Migration utility to transfer data from in-memory store to Supabase
export async function migrateDataToSupabase() {
  console.log('Starting migration to Supabase...')
  
  try {
    // Migrate users first (needed for foreign key relationships)
    console.log('Migrating users...')
    const users = dataStore.getAllUsers()
    for (const user of users) {
      try {
        await supabaseStore.addUser(user)
        console.log(` Migrated user: ${user.email}`)
      } catch (error: any) {
        if (error.code === '23505') { // Unique constraint violation
          console.log(`- User already exists: ${user.email}`)
        } else {
          console.error(` Failed to migrate user ${user.email}:`, error.message)
        }
      }
    }

    // Migrate memberships
    console.log('Migrating memberships...')
    const memberships = dataStore.getAllUsers()
      .filter(user => user.membershipId)
      .map(user => {
        const membership = dataStore.getMembership(user.id)
        return membership
      })
      .filter(Boolean)

    for (const membership of memberships) {
      if (membership) {
        try {
          await supabaseStore.addMembership(membership)
          console.log(` Migrated membership: ${membership.id}`)
        } catch (error: any) {
          if (error.code === '23505') {
            console.log(`- Membership already exists: ${membership.id}`)
          } else {
            console.error(` Failed to migrate membership ${membership.id}:`, error.message)
          }
        }
      }
    }

    // Migrate coaches
    console.log('Migrating coaches...')
    const coaches = dataStore.getAllCoaches()
    for (const coach of coaches) {
      try {
        await supabaseStore.addCoach(coach)
        console.log(` Migrated coach: ${coach.name}`)
      } catch (error: any) {
        if (error.code === '23505') {
          console.log(`- Coach already exists: ${coach.name}`)
        } else {
          console.error(` Failed to migrate coach ${coach.name}:`, error.message)
        }
      }
    }

    // Migrate leagues
    console.log('Migrating leagues...')
    const leagues = dataStore.getAllLeagues()
    for (const league of leagues) {
      try {
        await supabaseStore.addLeague(league)
        console.log(` Migrated league: ${league.name}`)
      } catch (error: any) {
        if (error.code === '23505') {
          console.log(`- League already exists: ${league.name}`)
        } else {
          console.error(` Failed to migrate league ${league.name}:`, error.message)
        }
      }
    }

    // Migrate bookings
    console.log('Migrating bookings...')
    const bookings = dataStore.getAllBookings()
    for (const booking of bookings) {
      try {
        await supabaseStore.addBooking(booking)
        console.log(` Migrated booking: ${booking.id}`)
      } catch (error: any) {
        if (error.code === '23505') {
          console.log(`- Booking already exists: ${booking.id}`)
        } else {
          console.error(` Failed to migrate booking ${booking.id}:`, error.message)
        }
      }
    }

    // Migrate gift cards
    console.log('Migrating gift cards...')
    const giftCards = dataStore.getAllGiftCards()
    for (const giftCard of giftCards) {
      try {
        await supabaseStore.addGiftCard(giftCard)
        console.log(` Migrated gift card: ${giftCard.code}`)
      } catch (error: any) {
        if (error.code === '23505') {
          console.log(`- Gift card already exists: ${giftCard.code}`)
        } else {
          console.error(` Failed to migrate gift card ${giftCard.code}:`, error.message)
        }
      }
    }

    // Migrate discount codes
    console.log('Migrating discount codes...')
    const discountCodes = dataStore.getAllDiscountCodes()
    for (const discountCode of discountCodes) {
      try {
        await supabaseStore.addDiscountCode(discountCode)
        console.log(` Migrated discount code: ${discountCode.code}`)
      } catch (error: any) {
        if (error.code === '23505') {
          console.log(`- Discount code already exists: ${discountCode.code}`)
        } else {
          console.error(` Failed to migrate discount code ${discountCode.code}:`, error.message)
        }
      }
    }

    console.log(' Migration completed successfully!')
    return { success: true, message: 'All data migrated to Supabase' }

  } catch (error) {
    console.error(' Migration failed:', error)
    return { success: false, message: `Migration failed: ${error}` }
  }
}

// Test connection to Supabase
export async function testSupabaseConnection() {
  try {
    console.log('Testing Supabase connection...')
    const users = await supabaseStore.getAllUsers()
    console.log(` Connection successful! Found ${users.length} users in database.`)
    return { success: true, userCount: users.length }
  } catch (error) {
    console.error(' Supabase connection failed:', error)
    return { success: false, error: error }
  }
}
