import { clsx, type ClassValue } from "clsx"
import { twMerge } from "tailwind-merge"
import { format, parse, isAfter, isBefore, addMinutes, differenceInMinutes } from "date-fns"
import { OperatingHours, PricingTier } from "./types"

export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs))
}

// Operating hours configuration
export const OPERATING_HOURS: OperatingHours = {
  monday: { open: "10:00", close: "22:00" },
  tuesday: { open: "10:00", close: "22:00" },
  wednesday: { open: "10:00", close: "22:00" },
  thursday: { open: "10:00", close: "22:00" },
  friday: { open: "10:00", close: "24:00" },
  saturday: { open: "09:00", close: "24:00" },
  sunday: { open: "10:00", close: "22:00" },
}

// Pricing tiers
export const PRICING_TIERS: PricingTier[] = [
  {
    name: "Off-Peak",
    rate: 50,
    conditions: {
      days: ["monday", "tuesday", "wednesday", "thursday", "friday"],
      timeRange: { start: "10:00", end: "17:00" }
    }
  },
  {
    name: "Peak",
    rate: 60,
    conditions: {
      days: ["monday", "tuesday", "wednesday", "thursday", "friday"],
      timeRange: { start: "17:00", end: "24:00" }
    }
  },
  {
    name: "Weekend",
    rate: 60,
    conditions: {
      days: ["saturday", "sunday"],
      timeRange: { start: "09:00", end: "24:00" }
    }
  }
]

// Membership pricing
export const MEMBERSHIP_RATES = {
  monthly: 90,
  freeHours: 2,
  discountedRate: 40,
  membershipHourValue: 45
}

// Bay configuration
export const BAY_CONFIG = {
  totalBays: 5,
  maxBaysPerBooking: 2,
  maxSimultaneousBookings: 5
}

// Time slot utilities
export function generateTimeSlots(date: Date): string[] {
  const dayName = format(date, 'EEEE').toLowerCase()
  const hours = OPERATING_HOURS[dayName]
  
  if (!hours) return []
  
  const slots: string[] = []
  const startTime = parse(hours.open, 'HH:mm', new Date())
  const endTime = parse(hours.close === '24:00' ? '23:59' : hours.close, 'HH:mm', new Date())
  
  let currentTime = startTime
  
  while (isBefore(currentTime, endTime)) {
    slots.push(format(currentTime, 'HH:mm'))
    currentTime = addMinutes(currentTime, 30)
  }
  
  return slots
}

// Pricing calculation
export function calculateBookingPrice(
  date: Date,
  startTime: string,
  duration: number,
  numberOfBays: number,
  isMember: boolean = false,
  membershipHoursUsed: number = 0
): {
  basePrice: number
  membershipDiscount: number
  totalPrice: number
  membershipHoursApplied: number
} {
  const dayName = format(date, 'EEEE').toLowerCase()
  const timeSlot = parse(startTime, 'HH:mm', new Date())
  
  // Find applicable pricing tier
  let hourlyRate = 60 // default weekend rate
  
  for (const tier of PRICING_TIERS) {
    if (tier.conditions.days.includes(dayName)) {
      const tierStart = parse(tier.conditions.timeRange.start, 'HH:mm', new Date())
      const tierEnd = parse(tier.conditions.timeRange.end === '24:00' ? '23:59' : tier.conditions.timeRange.end, 'HH:mm', new Date())
      
      if (!isBefore(timeSlot, tierStart) && isBefore(timeSlot, tierEnd)) {
        hourlyRate = tier.rate
        break
      }
    }
  }
  
  const totalHours = duration * numberOfBays
  const basePrice = totalHours * hourlyRate
  
  if (!isMember) {
    return {
      basePrice,
      membershipDiscount: 0,
      totalPrice: basePrice,
      membershipHoursApplied: 0
    }
  }
  
  // Apply membership benefits
  const freeHoursAvailable = Math.max(0, MEMBERSHIP_RATES.freeHours - membershipHoursUsed)
  const freeHoursToApply = Math.min(freeHoursAvailable, totalHours)
  const paidHours = totalHours - freeHoursToApply
  
  const membershipDiscount = freeHoursToApply * hourlyRate + paidHours * (hourlyRate - MEMBERSHIP_RATES.discountedRate)
  const totalPrice = basePrice - membershipDiscount
  
  return {
    basePrice,
    membershipDiscount,
    totalPrice,
    membershipHoursApplied: freeHoursToApply
  }
}

// Validation utilities
export function isValidTimeSlot(date: Date, time: string): boolean {
  const dayName = format(date, 'EEEE').toLowerCase()
  const hours = OPERATING_HOURS[dayName]
  
  if (!hours) return false
  
  const timeSlot = parse(time, 'HH:mm', new Date())
  const openTime = parse(hours.open, 'HH:mm', new Date())
  const closeTime = parse(hours.close === '24:00' ? '23:59' : hours.close, 'HH:mm', new Date())
  
  return !isBefore(timeSlot, openTime) && isBefore(timeSlot, closeTime)
}

export function isValidDuration(startTime: string, duration: number, date: Date): boolean {
  const dayName = format(date, 'EEEE').toLowerCase()
  const hours = OPERATING_HOURS[dayName]
  
  if (!hours) return false
  
  const start = parse(startTime, 'HH:mm', new Date())
  const end = addMinutes(start, duration * 60)
  const closeTime = parse(hours.close === '24:00' ? '23:59' : hours.close, 'HH:mm', new Date())
  
  return !isAfter(end, closeTime) && duration >= 1 && duration <= 4
}

// Gift card utilities
export function generateGiftCardCode(): string {
  const chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789'
  let result = ''
  for (let i = 0; i < 12; i++) {
    result += chars.charAt(Math.floor(Math.random() * chars.length))
  }
  return result
}

// Email validation
export function isValidEmail(email: string): boolean {
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
  return emailRegex.test(email)
}

// Phone validation
export function isValidPhone(phone: string): boolean {
  const phoneRegex = /^\+?[\d\s\-\(\)]{10,}$/
  return phoneRegex.test(phone)
}

// Format currency
export function formatCurrency(amount: number): string {
  return new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD'
  }).format(amount)
}

// Format time
export function formatTime(time: string): string {
  const parsed = parse(time, 'HH:mm', new Date())
  return format(parsed, 'h:mm a')
}

// Calculate duration between times
export function calculateDuration(startTime: string, endTime: string): number {
  const start = parse(startTime, 'HH:mm', new Date())
  const end = parse(endTime, 'HH:mm', new Date())
  return differenceInMinutes(end, start) / 60
}

