import { supabase, supabaseAdmin } from './supabase'
import { Booking, User, Membership, Coach, League, GiftCard, DiscountCode, CoachAvailability } from './types'

// Supabase-based data store implementation
export class SupabaseStore {
  // User methods
  async addUser(user: User): Promise<User> {
    const { data, error } = await supabaseAdmin
      .from('users')
      .insert([{
        id: user.id,
        name: user.name,
        email: user.email,
        phone: user.phone,
        password: user.password,
        membership_id: user.membershipId,
        is_admin: user.isAdmin || false,
        created_at: user.createdAt.toISOString()
      }])
      .select()
      .single()

    if (error) throw error
    return this.mapUserFromDb(data)
  }

  async getUser(id: string): Promise<User | null> {
    const { data, error } = await supabase
      .from('users')
      .select('*')
      .eq('id', id)
      .single()

    if (error) {
      if (error.code === 'PGRST116') return null // Not found
      throw error
    }
    return this.mapUserFromDb(data)
  }

  async getUserByEmail(email: string): Promise<User | null> {
    const { data, error } = await supabase
      .from('users')
      .select('*')
      .eq('email', email)
      .single()

    if (error) {
      if (error.code === 'PGRST116') return null // Not found
      throw error
    }
    return this.mapUserFromDb(data)
  }

  async getAllUsers(): Promise<User[]> {
    const { data, error } = await supabase
      .from('users')
      .select('*')
      .order('created_at', { ascending: false })

    if (error) throw error
    return data.map(this.mapUserFromDb)
  }

  // Booking methods
  async addBooking(booking: Booking): Promise<Booking> {
    const { data, error } = await supabase
      .from('bookings')
      .insert([{
        id: booking.id,
        user_id: booking.userId,
        guest_name: booking.guestName,
        guest_email: booking.guestEmail,
        guest_phone: booking.guestPhone,
        date: booking.date.toISOString().split('T')[0],
        start_time: booking.startTime,
        end_time: booking.endTime,
        duration: booking.duration,
        number_of_bays: booking.numberOfBays,
        number_of_people: booking.numberOfPeople,
        bay_numbers: booking.bayNumbers,
        total_price: booking.totalPrice,
        type: booking.type,
        status: booking.status,
        created_at: booking.createdAt.toISOString()
      }])
      .select()
      .single()

    if (error) throw error
    return this.mapBookingFromDb(data)
  }

  async getBooking(id: string): Promise<Booking | null> {
    const { data, error } = await supabase
      .from('bookings')
      .select('*')
      .eq('id', id)
      .single()

    if (error) {
      if (error.code === 'PGRST116') return null // Not found
      throw error
    }
    return this.mapBookingFromDb(data)
  }

  async getAllBookings(): Promise<Booking[]> {
    const { data, error } = await supabase
      .from('bookings')
      .select('*')
      .order('created_at', { ascending: false })

    if (error) throw error
    return data.map(this.mapBookingFromDb)
  }

  async updateBooking(id: string, updates: Partial<Booking>): Promise<boolean> {
    const dbUpdates: any = {}
    
    if (updates.userId !== undefined) dbUpdates.user_id = updates.userId
    if (updates.guestName !== undefined) dbUpdates.guest_name = updates.guestName
    if (updates.guestEmail !== undefined) dbUpdates.guest_email = updates.guestEmail
    if (updates.guestPhone !== undefined) dbUpdates.guest_phone = updates.guestPhone
    if (updates.date !== undefined) dbUpdates.date = updates.date.toISOString().split('T')[0]
    if (updates.startTime !== undefined) dbUpdates.start_time = updates.startTime
    if (updates.endTime !== undefined) dbUpdates.end_time = updates.endTime
    if (updates.duration !== undefined) dbUpdates.duration = updates.duration
    if (updates.numberOfBays !== undefined) dbUpdates.number_of_bays = updates.numberOfBays
    if (updates.numberOfPeople !== undefined) dbUpdates.number_of_people = updates.numberOfPeople
    if (updates.bayNumbers !== undefined) dbUpdates.bay_numbers = updates.bayNumbers
    if (updates.totalPrice !== undefined) dbUpdates.total_price = updates.totalPrice
    if (updates.type !== undefined) dbUpdates.type = updates.type
    if (updates.status !== undefined) dbUpdates.status = updates.status

    const { error } = await supabase
      .from('bookings')
      .update(dbUpdates)
      .eq('id', id)

    if (error) throw error
    return true
  }

  async deleteBooking(id: string): Promise<boolean> {
    const { error } = await supabase
      .from('bookings')
      .delete()
      .eq('id', id)

    if (error) throw error
    return true
  }

  // Membership methods
  async addMembership(membership: Membership): Promise<Membership> {
    const { data, error } = await supabase
      .from('memberships')
      .insert([{
        id: membership.id,
        user_id: membership.userId,
        status: membership.status,
        free_hours_remaining: membership.freeHoursRemaining,
        billing_date: membership.billingDate.toISOString(),
        created_at: membership.createdAt.toISOString()
      }])
      .select()
      .single()

    if (error) throw error
    return this.mapMembershipFromDb(data)
  }

  async getMembership(userId: string): Promise<Membership | null> {
    const { data, error } = await supabase
      .from('memberships')
      .select('*')
      .eq('user_id', userId)
      .single()

    if (error) {
      if (error.code === 'PGRST116') return null // Not found
      throw error
    }
    return this.mapMembershipFromDb(data)
  }

  // Coach methods
  async addCoach(coach: Coach): Promise<Coach> {
    const { data, error } = await supabase
      .from('coaches')
      .insert([{
        id: coach.id,
        name: coach.name,
        email: coach.email,
        phone: coach.phone,
        photo: coach.photo,
        created_at: coach.createdAt.toISOString()
      }])
      .select()
      .single()

    if (error) throw error

    // Insert availability records
    if (coach.availability.length > 0) {
      const { error: availError } = await supabase
        .from('coach_availability')
        .insert(coach.availability.map(avail => ({
          id: avail.id,
          coach_id: avail.coachId,
          day_of_week: avail.dayOfWeek,
          start_time: avail.startTime,
          end_time: avail.endTime,
          is_available: avail.isAvailable
        })))

      if (availError) throw availError
    }

    return this.mapCoachFromDb(data, coach.availability)
  }

  async getCoach(id: string): Promise<Coach | null> {
    const { data, error } = await supabase
      .from('coaches')
      .select('*')
      .eq('id', id)
      .single()

    if (error) {
      if (error.code === 'PGRST116') return null // Not found
      throw error
    }

    // Get availability
    const { data: availData, error: availError } = await supabase
      .from('coach_availability')
      .select('*')
      .eq('coach_id', id)

    if (availError) throw availError

    const availability = availData.map(this.mapAvailabilityFromDb)
    return this.mapCoachFromDb(data, availability)
  }

  async getAllCoaches(): Promise<Coach[]> {
    const { data, error } = await supabase
      .from('coaches')
      .select('*')
      .order('created_at', { ascending: false })

    if (error) throw error

    // Get all availability records
    const { data: availData, error: availError } = await supabase
      .from('coach_availability')
      .select('*')

    if (availError) throw availError

    return data.map(coach => {
      const availability = availData
        .filter(avail => avail.coach_id === coach.id)
        .map(this.mapAvailabilityFromDb)
      return this.mapCoachFromDb(coach, availability)
    })
  }

  // League methods
  async addLeague(league: League): Promise<League> {
    const { data, error } = await supabase
      .from('leagues')
      .insert([{
        id: league.id,
        name: league.name,
        description: league.description,
        format: league.format,
        match_type: league.matchType,
        price_per_person: league.pricePerPerson,
        max_teams: league.maxTeams,
        bay_allocation: league.bayAllocation,
        start_date: league.startDate.toISOString().split('T')[0],
        end_date: league.endDate.toISOString().split('T')[0],
        day_of_week: league.dayOfWeek,
        start_time: league.startTime,
        number_of_weeks: league.numberOfWeeks,
        status: league.status,
        created_at: league.createdAt.toISOString()
      }])
      .select()
      .single()

    if (error) throw error
    return this.mapLeagueFromDb(data)
  }

  async getLeague(id: string): Promise<League | null> {
    const { data, error } = await supabase
      .from('leagues')
      .select('*')
      .eq('id', id)
      .single()

    if (error) {
      if (error.code === 'PGRST116') return null // Not found
      throw error
    }
    return this.mapLeagueFromDb(data)
  }

  async getAllLeagues(): Promise<League[]> {
    const { data, error } = await supabase
      .from('leagues')
      .select('*')
      .order('created_at', { ascending: false })

    if (error) throw error
    return data.map(this.mapLeagueFromDb)
  }

  // Gift card methods
  async addGiftCard(giftCard: GiftCard): Promise<GiftCard> {
    const { data, error } = await supabase
      .from('gift_cards')
      .insert([{
        id: giftCard.id,
        code: giftCard.code,
        amount: giftCard.amount,
        remaining_balance: giftCard.remainingBalance,
        purchaser_name: giftCard.purchaserName,
        purchaser_email: giftCard.purchaserEmail,
        recipient_name: giftCard.recipientName,
        recipient_email: giftCard.recipientEmail,
        status: giftCard.status,
        created_at: giftCard.createdAt.toISOString(),
        redeemed_at: giftCard.redeemedAt?.toISOString()
      }])
      .select()
      .single()

    if (error) throw error
    return this.mapGiftCardFromDb(data)
  }

  async getGiftCard(code: string): Promise<GiftCard | null> {
    const { data, error } = await supabase
      .from('gift_cards')
      .select('*')
      .eq('code', code)
      .single()

    if (error) {
      if (error.code === 'PGRST116') return null // Not found
      throw error
    }
    return this.mapGiftCardFromDb(data)
  }

  async getAllGiftCards(): Promise<GiftCard[]> {
    const { data, error } = await supabase
      .from('gift_cards')
      .select('*')
      .order('created_at', { ascending: false })

    if (error) throw error
    return data.map(this.mapGiftCardFromDb)
  }

  // Discount code methods
  async addDiscountCode(discountCode: DiscountCode): Promise<DiscountCode> {
    const { data, error } = await supabase
      .from('discount_codes')
      .insert([{
        id: discountCode.id,
        code: discountCode.code,
        description: discountCode.description,
        type: discountCode.type,
        value: discountCode.value,
        max_uses: discountCode.maxUses,
        current_uses: discountCode.currentUses,
        valid_from: discountCode.validFrom.toISOString(),
        valid_until: discountCode.validUntil.toISOString(),
        status: discountCode.status,
        created_at: discountCode.createdAt.toISOString()
      }])
      .select()
      .single()

    if (error) throw error
    return this.mapDiscountCodeFromDb(data)
  }

  async getDiscountCode(code: string): Promise<DiscountCode | null> {
    const { data, error } = await supabase
      .from('discount_codes')
      .select('*')
      .eq('code', code)
      .single()

    if (error) {
      if (error.code === 'PGRST116') return null // Not found
      throw error
    }
    return this.mapDiscountCodeFromDb(data)
  }

  async getAllDiscountCodes(): Promise<DiscountCode[]> {
    const { data, error } = await supabase
      .from('discount_codes')
      .select('*')
      .order('created_at', { ascending: false })

    if (error) throw error
    return data.map(this.mapDiscountCodeFromDb)
  }

  // Mapping functions to convert between database format and TypeScript interfaces
  private mapUserFromDb(data: any): User {
    return {
      id: data.id,
      name: data.name,
      email: data.email,
      phone: data.phone,
      password: data.password,
      membershipId: data.membership_id,
      isAdmin: data.is_admin,
      createdAt: new Date(data.created_at)
    }
  }

  private mapBookingFromDb(data: any): Booking {
    return {
      id: data.id,
      userId: data.user_id,
      guestName: data.guest_name,
      guestEmail: data.guest_email,
      guestPhone: data.guest_phone,
      date: new Date(data.date),
      startTime: data.start_time,
      endTime: data.end_time,
      duration: data.duration,
      numberOfBays: data.number_of_bays,
      numberOfPeople: data.number_of_people,
      bayNumbers: data.bay_numbers,
      totalPrice: data.total_price,
      type: data.type,
      status: data.status,
      createdAt: new Date(data.created_at)
    }
  }

  private mapMembershipFromDb(data: any): Membership {
    return {
      id: data.id,
      userId: data.user_id,
      status: data.status,
      freeHoursRemaining: data.free_hours_remaining,
      billingDate: new Date(data.billing_date),
      createdAt: new Date(data.created_at)
    }
  }

  private mapCoachFromDb(data: any, availability: CoachAvailability[]): Coach {
    return {
      id: data.id,
      name: data.name,
      email: data.email,
      phone: data.phone,
      photo: data.photo,
      availability,
      createdAt: new Date(data.created_at)
    }
  }

  private mapAvailabilityFromDb(data: any): CoachAvailability {
    return {
      id: data.id,
      coachId: data.coach_id,
      dayOfWeek: data.day_of_week,
      startTime: data.start_time,
      endTime: data.end_time,
      isAvailable: data.is_available
    }
  }

  private mapLeagueFromDb(data: any): League {
    return {
      id: data.id,
      name: data.name,
      description: data.description,
      format: data.format,
      matchType: data.match_type,
      pricePerPerson: data.price_per_person,
      maxTeams: data.max_teams,
      bayAllocation: data.bay_allocation,
      startDate: new Date(data.start_date),
      endDate: new Date(data.end_date),
      dayOfWeek: data.day_of_week,
      startTime: data.start_time,
      numberOfWeeks: data.number_of_weeks,
      status: data.status,
      createdAt: new Date(data.created_at)
    }
  }

  private mapGiftCardFromDb(data: any): GiftCard {
    return {
      id: data.id,
      code: data.code,
      amount: data.amount,
      remainingBalance: data.remaining_balance,
      purchaserName: data.purchaser_name,
      purchaserEmail: data.purchaser_email,
      recipientName: data.recipient_name,
      recipientEmail: data.recipient_email,
      status: data.status,
      createdAt: new Date(data.created_at),
      redeemedAt: data.redeemed_at ? new Date(data.redeemed_at) : undefined
    }
  }

  private mapDiscountCodeFromDb(data: any): DiscountCode {
    return {
      id: data.id,
      code: data.code,
      description: data.description,
      type: data.type,
      value: data.value,
      maxUses: data.max_uses,
      currentUses: data.current_uses,
      validFrom: new Date(data.valid_from),
      validUntil: new Date(data.valid_until),
      status: data.status,
      createdAt: new Date(data.created_at)
    }
  }
}

// Export singleton instance
export const supabaseStore = new SupabaseStore()
